
  
  
  .unvisited{
    border: 1px solid rgb(175, 216, 248);
    background-color:white;
    font-size: 12px;
    color: rgb(81, 81, 90);
  }
  .obstruction{
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-animation-name: obstructionAnimation;
            animation-name: obstructionAnimation;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    width: 20px;
    height: 20px;
    font-size: 12px;
    color: rgb(81, 81, 90);
  }
  
  @-webkit-keyframes obstructionAnimation {
    0% {
      -webkit-transform: scale(.3);
              transform: scale(.3);
      background-color: rgb(7, 29, 39);
    }
  
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color:  rgb(7, 29, 39);
    }
  
    100% {
      -webkit-transform: scale(1.0);
              transform: scale(1.0);
      background-color:  rgb(7, 29, 39);
    }
  }
  
  @keyframes obstructionAnimation {
    0% {
      -webkit-transform: scale(.3);
              transform: scale(.3);
      background-color: rgb(7, 29, 39);
    }
  
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color:  rgb(7, 29, 39);
    }
  
    100% {
      -webkit-transform: scale(1.0);
              transform: scale(1.0);
      background-color:  rgb(7, 29, 39);
    }
  }
  


  .visited{
    border: 1px solid rgb(175, 216, 248);
    -webkit-animation-name: visitedAnimation;
            animation-name: visitedAnimation;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    font-size: 12px;
    color: rgb(81, 81, 90);
  }
  @-webkit-keyframes visitedAnimation {
    0% {
      -webkit-transform: scale(.3);
              transform: scale(.3);
      background-color:  rgba(6, 4, 41, 0.75);
      
    }
  
    50% {
      background-color: rgba(1, 3, 92, 0.75);
    }
  
    75% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color: rgba(38, 75, 160, 0.904)
    }
  
    100% {
      -webkit-transform: scale(1.0);
              transform: scale(1.0);
      background-color: rgba(109, 99, 224, 0.75);
    }
  }
  @keyframes visitedAnimation {
    0% {
      -webkit-transform: scale(.3);
              transform: scale(.3);
      background-color:  rgba(6, 4, 41, 0.75);
      
    }
  
    50% {
      background-color: rgba(1, 3, 92, 0.75);
    }
  
    75% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color: rgba(38, 75, 160, 0.904)
    }
  
    100% {
      -webkit-transform: scale(1.0);
              transform: scale(1.0);
      background-color: rgba(109, 99, 224, 0.75);
    }
  }



  .path{
    background-color: rgb(27, 226, 160);
    -webkit-animation-name: triangletwo;
            animation-name: triangletwo;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
    font-size: 12px;
    color: rgb(81, 81, 90);
  }
  
  
  @-webkit-keyframes triangletwo {
    0% {
      -webkit-transform: scale(.6);
              transform: scale(.6);
      background-color:rgb(39, 200, 74);
    }
  
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color:rgb(39, 200, 74);
    }
  
    100% {
      -webkit-transform: scale(1.0);
              transform: scale(1.0);
      background-color: rgb(39, 200, 74);
    }
  }
  
  
  @keyframes triangletwo {
    0% {
      -webkit-transform: scale(.6);
              transform: scale(.6);
      background-color:rgb(39, 200, 74);
    }
  
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      background-color:rgb(39, 200, 74);
    }
  
    100% {
      -webkit-transform: scale(1.0);
              transform: scale(1.0);
      background-color: rgb(39, 200, 74);
    }
  }


